<template>
  <div id="dashboard">
      <h3>Dashboard</h3>
      <ul class="collection with-header">
          <li class="collection-header">
              <h4>Employees</h4>
          </li>
          
          <!-- Use v-for to render the employees list, and get access through the employee id from firebase -->
          <li v-for="employee in employees" 
          v-bind:key="employee.id" class="collection-item">
          <!-- chip is like a label class in bootstrap -->
          <div class="chip">{{employee.dept}}</div>
          {{employee.employee_id}}:{{employee.name}}
           
      <router-link class="secondary-content" v-bind:to="{name:
      'view-employee', params: {employee_id: employee.employee_id}}">
      <i class="material-icons">remove_red_eye</i>
      </router-link>
    </li>
</ul>
      <!-- Add Floating Create User Button -->
      <div class="fixed-action-btn">
          <router-link to="/new" class="btn-floating btn-large blue">
          <i class="material-icons">add</i>
          </router-link>
      </div>
  </div>
</template>

<script>
// Import database
import db from './firebaseInit'
export default {
  name: "dashboard",
  data () {
      return {
          employees : []
      }
  },
  created () {
      db.collection('employees').orderBy('dept').get().then(querySnapshot => {
          querySnapshot.forEach(doc => {
              const data = {
                  'id': doc.id,
                  'employee_id': doc.data().employee_id,
                  'name': doc.data().name,
                  'dept': doc.data().dept,
                  'position': doc.data().position
              }
              // Push data Onto Employees Array
              this.employees.push(data)
          })
      })
    }
}
</script>




